import React from "react"
import { graphql } from 'gatsby'

import Layout from "../components/layout"
import SEO from "../components/seo"
import Parser from "html-react-parser"
import { getShareButton } from '../components/Careers/PositionCard/positionCard'

class Job extends React.Component {
  render() {
    const { description, lists, applyUrl, categories, text, descriptionPlain, id } = this.props.data.jobsJson
    console.log('DEscription plain', this.props.data.jobsJson);
    return (
      <Layout location={this.props.location}>
        <SEO
          title={text}
          description={ descriptionPlain }
        />
        <div className="container first-section">
          <div className="d-flex flex-row flex-wrap pb-5 mt-5">
            <div
              id={id}
              name={id}
              className="col-md-12  mt-5  col-12 col-md-6 mt-4 mb-0 px-2"
            >
              <div className=" scroll  d-flex flex-column flex-wrap justify-content-center careers-position careers-position-unhighlight border-success pt-3">
                <i className="fa scroll ml-auto pr-5 fa-minus  mt-auto text-green "></i>
                <h6 className=" px-4 roboto-regular font-sm text-dark ">
                  {text}
                </h6>
                <div className="d-flex flex-row flex-wrap roboto-black font-xs text-uppercase">
                  <span className="pl-4  text-green col-">{categories.location}</span>
                  <span className="pl-4  text-black col-">{categories.team}</span>
                </div>
                <div>
                  <div className="pl-4 pt-3">
                    <div className="row justify-content-between align-items-center pl-2">
                      <a
                        target="_blank"
                        rel="noopener noreferrer"
                        href={applyUrl}
                        className="apply btn btn-success px-5 apply  my-2"
                      >
                        APPLY NOW
                      </a>
                      <div className="d-none d-md-block">
                                  {getShareButton(this.props.data.jobsJson)}
                                </div>
                    </div>
                    <div className="py-5 my-5 py-md-2 my-md-0">
                      <p className="  roboto-bold text-black font-sm">
                        Overview
                      </p>
                      <div className=" description-font text-dark col-12 pl-1">
                        {Parser(description)}
                      </div>
                      <div className="">
                        {lists.map((list, i) => {
                          return (
                            <React.Fragment>
                              <p className="pt-3 roboto-bold text-black font-sm">
                                {list.text}
                              </p>
                              <ul className="pl-3 mb-3">
                                  {Parser(list.content)}
                              </ul>
                            </React.Fragment>
                          )
                        })}
                      </div>
                    </div>
                    <div className="d-block text-center"></div>
                  </div>
                  <div className="w-100 text-center">
                    <a
                      target="_blank"
                      rel="noopener noreferrer"
                      href="https://jobs.lever.co/gojek/838723ed-65d3-4596-9b17-67ec695b9992/apply?lever-source[]=gojek.io"
                      className="apply btn btn-success px-5 apply col-6 col-md-4  my-5"
                    >
                      APPLY FOR THIS JOB{" "}
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    )
  }
}

export default Job
export const pageQuery = graphql`
         query PostPage($id: String!) {
           # Select the post which equals this id.
           jobsJson(id: { eq: $id }) {
             id
             text
             hostedUrl
             descriptionPlain
             description
             createdAt
             applyUrl
             additionalPlain
             additional
             lists {
               content
               text
             }
             categories {
               commitment
               department
               location
               team
             }
           }
         }
       `
